.testimonials-container{
margin:auto;
margin-top:40px;
}

.tests{
    margin:auto;
    margin-top:40px;
    display:grid;
    grid-template-columns: 1fr 1fr 1fr;
    width:1000px;
}

.test{
    margin: auto;
    width:300px;
    padding:20px;
    padding-left:0px;
    margin-left:10px;
    background:rgba(29, 29, 29, 0.532);
    border-radius:2%;
}
.test h5{
    width:200px;
    margin:60px;
    margin-top:-10px;
}
.test h6{
    margin-top:-50px;
    color:aquamarine;
    font-size:10px;
}
.quotes{
    padding:10px;
    margin-left:-150px;
    color:var(--color-primary-variant);
    width: 30px;
    height:30px;
}