@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

html{scroll-behavior:smooth}
::-webkit-scrollbar{
  display:none;
}
:root{
--color-bg:rgba(0, 0, 0, 0.928);
--color-bg-variant:black;
--color-primary:rgba(89, 4, 247, 0.668);
--color-primary-variant:rgba(136, 0, 255, 0.631);
--color-white:white;
--color-light:rgba(255, 253, 253, 0.227);

--transition:all 400ms ease ;


--container-width-lg:75%;
--container-width-md:86%;
--container-width-sm:90%;
}

body{
  font-family:'Poppins',sans-serif;
  background-color:var(--color-bg);
  color:white;
  background-image: url("./components/bg-texture.png");
}

.container{
  display: grid;
  justify-content: center;
  text-align: center;
  width:var(--container-width-lg);

}

.btn{
  display: inline-block;
  background-color:var(--color-primary);
  font-size:15px;
  color:white;
  padding:10px;
  border-radius: 0.4rem;
  cursor:pointer;
  border:1px solid var(--color-primary);
  transition: var(--transition);
  margin:10px;
  text-decoration: none;
}

.btn-primary{
background: var(--color-primary);
}

.btn-black{
  background:#0000;
  border-color:#0000;
}

.btn:hover{
  background: var(--color-white);
  color:var(--color-bg);
  border-color: transparent;
}

h5{
  margin-top:-10px;
  color:var(--color-light);
}

hr{
  border-color:var(--color-light);
}