.content-container{
    margin:auto;    
}


.cards{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap:1rem;
    width:1000px;
    margin:auto;
    justify-content: center;
    text-align: center;
    margin-left:100px;
    
}

.card{
    width:250px;
    margin-top:50px;
    display: grid;
    text-align: center;
    justify-content: center;

    overflow:-moz-hidden-unscrollable;
}

.card h5{
    width:200px;
    margin:10px;
    margin-top:-10px;

}


.star{
    width: 30px;
    height:30px;
    background: var(--color-primary-variant);
    padding:5px;
    border-radius:100%;
    margin-left:80px;
}