.footer-container{
    display:grid;
    text-align: center;
    line-height: 100px;
    background:var(--color-primary);
    margin-bottom:-30px;
}

.footer_socials{
    display:flex;
    justify-content: center;
    gap:1rem;
    margin-bottom:4rem;
}

.footer_socials a{
    background: var(--color-bg);
    color:var(--color-white);
    padding:0.8rem;
    border-radius: 0.7rem;
    display:flex;
    border:1px solid transparent;
}